<template>
	<div class="shop">
		 <div class="slider-wrapper" v-if="shopList.length">
          <slider>
            <div v-for="item in shopList">
              <a :href="item.url">
                <img :src="item.image" style="width:100%">
              </a>
            </div>
          </slider>
        </div>
        <div class="menu">
        	<ul>
        		<li>
        			<router-link tag="div" to="/all">
        				<img src="http://img5.mtime.cn/mg/2017/02/07/161342.70896250.jpg" alt="" />
        				<span>全部</span>
        			</router-link>
        		</li>
        		<li>
        			<router-link tag="div" to="/shuma">
        				<img src="http://img5.mtime.cn/mg/2017/02/07/161141.82690725.jpg" alt="" />
        				<span>数码</span>
        			</router-link>
        			
        		</li>
        		<li>
        			<router-link tag="div" to="/fushi">
        				<img src="http://img5.mtime.cn/mg/2017/02/07/161148.25620959.jpg" alt="" />
        				<span>服饰</span>
        			</router-link>
        		</li>
        		<li>
        			<router-link tag="div" to="/jiaju">
        				<img src="http://img5.mtime.cn/mg/2017/02/07/161155.82939104.jpg" alt="" />
        				<span>家居</span>
        			</router-link>
        		</li>
        		<li>
        			<router-link tag="div" to="/xingzhan">
        				<img src="http://img5.mtime.cn/mg/2017/02/07/161224.10155577.jpg" alt="" />
        				<span>星战</span>
        			</router-link>
        		</li>
        		<li>
        			<router-link tag="div" to="/manwei">
        				<img src="http://img5.mtime.cn/mg/2017/02/07/161315.97010262.jpg" alt="" />
        				<span>漫威</span>
        			</router-link>
        		</li>
        		<li>
        			<router-link tag="div" to="/fuchao">
        				<img src="http://img5.mtime.cn/mg/2017/02/07/161325.50615572.jpg" alt="" />
        				<span>蝠超</span>
        			</router-link>
        		</li>
        		<li>
        			<router-link tag="div" class="mowan" to="/mowan">
        				<img src="http://img5.mtime.cn/mg/2017/02/07/161133.99290860.jpg" alt="" />
        				<span>模玩</span>
        				<keep-alive>
          					<router-view></router-view>
        				</keep-alive>
        			</router-link>
        		</li>
        	</ul>
        </div>
        <div class="line"></div>
      	<div class="tabList">
      		<ul class="commodityList-list">
				<li v-for="item in commodityList" class="commodityList-item" >
					<span class="iconText">{{item.iconText}}</span>
					<img v-lazy="item.image"/>	
					<p>
						<span class="name">{{item.name}}</span>
						<span class="price">￥{{item.minSalePrice/100}}</span>
					</p>
				</li>
			</ul>
      	</div>
      
		  	<div class="copying">
		  		<p>
		  			<span>Copyright </span>
		  			<span> HiMovies.com Inc. All rights reserved.</span>
		  		</p>
		  	</div>
		<!--<div class="loading-container" v-show="!shopList.length">
        	<loading></loading>
      	</div>-->
	</div>
</template>

<script>
	import Slider from "base/slider/slider"
    import Loading from "base/loading/loading"
	import axios from "axios"
	export default {
    	data(){
    		return{
    			shopList:[],
    			commodityList:[]
    		}
    	},
	    components:{
	    	Loading,
	    	Slider
	    },
	    created(){
	      	this._getShop();
	      	this._getCommodity();
	    },
	    methods:{
	      	_getShop(){
	      		axios.get("http://120.78.84.152:8080/api/getShop",{}).then((res)=>{
	      			console.log(res.data)
	      			this.shopList = res.data;
	      		})
	      	},
	      	_getCommodity(){
	      		axios.get("http://120.78.84.152:8080/api/getCommodity",{}).then((res)=>{
	      			console.log(res.data)
	      			this.commodityList = res.data;
	      		})
	      	}
	    }
    }
</script>

<style scoped lang="stylus">
.shop{
	position: relative;
	top:8.5rem;
	width: 100%;
}
.slider-wrapper{
	position: relative
    width: 100%
    overflow: hidden
}
.menu{
	margin-top: 1rem;
	width: 100%;
	height: 15rem;
	text-align: center;
} 
.menu ul{
	display: block;
	width: 100%;
	height: 100%;	
}
.menu li{
	float: left;
	width: 25%;
	height: 8rem;
} 
.menu li img{
	width: 4.8rem;
	width: 4.8rem;
}
.menu li span{
	display: block;
	font-size:1rem ;
	
} 
.line{
	width: 100%;
	height: .5rem;
	background:#f1f1f1;
}  
.tabList{
	float: left;
	width: 100%;
}     
.commodityList-list{
	width: 100%;
	float: left;
	background: #f1f1f1;
}
.commodityList-item{
	float: left;
	padding: .2rem .2rem;
	width: 50%;
	position: relative;
}
.commodityList-item .iconText{
	position: absolute;
	top: .2rem;
	left: .1rem;
	width: 2.5rem;
	height: 1.5rem;
	line-height: 1.5rem;
	text-align: center;
	font-size: 1rem;
	color: #fff;
	background: #25ACBD;
	
}
.commodityList-item img{
	width: 100%;
	display: block;
}
.commodityList-item p{
	padding: .1rem .4rem;
	width: 100%;
	height: 6rem;
	background: #fff;
	
}
.commodityList-item p .name{
	padding: 0 .5rem;
	height: 3.2rem;
	display: block;
	font-size:1.2rem ;
}
.commodityList-item p .price{
	display: block;
	padding-left: .5rem;
	font:1.4rem "微软雅黑";
	color: #ff8600;
}
/*footer部分*/	
.tab{
	width: 100%;
	height: 3rem;	

}
.tab-item{
	width: 25%
	height: 3rem
	float:left
	text-align:center	
}
.tab-link{
	display:block
	width: 100%;
	height:3rem;
	color:#1c2635
	font:1.4rem/3rem "微软雅黑"	
}			
.copying{
	float: left;
	width: 100%
	height: 4rem
	background:#f1f1f1
	line-height: 4rem
	text-align:center
	font-size:1.2rem
	color:#ccc	
}
.loading-container{
	position: absolute
	left:50%
	top:13rem
	transform: translateX(-50%)
}
</style>


