<template>
	<div class="home-page" >
		<div class="home-page-hot">
			<div class="home-page-hot-list">
			    <h1 class="list-title">正在热映({{hotList.length}}部)
			        <router-link tag="div" class="list-hot-item" to="/hot-more">
				       	<span class="hot-more-link"><img src="../../common/images/go.png"/></span>
				        <keep-alive>
          					<router-view></router-view>
        				</keep-alive>
				    </router-link>
			    </h1>
			
			    		<ul>
				        	<li v-for="item in hotList" class="hot-item" >
				            	<div class="hot-move" >
				                	<a :href="'http://m.mtime.cn/#!/movie/'+item.id+'/'">
				               		<div class="score" v-html="item.r>0?item.r:''"></div>
				               		<img v-lazy="item.img"></a>
				            	   	<div class="text">
				               			<span class="name" v-html="item.t" ></span>
				           			</div>
				            	</div>
				        	</li>
				    	</ul>	   
			  
			    <div class="loading-container" v-show="!hotList.length">
        			<loading></loading>
      			</div>
			    </div>
		        <div class="about-to-show">
		        	<h1 class="list-aboutToShow-title">即将上映({{aboutToShow.length}}部)
			    	<router-link tag="div" class="list-aboutToShow-item" to="/aboutToShow-more">
				       	<span class="aboutToShow-more-link"><img src="../../common/images/go.png"/></span>
				        <keep-alive>
          					<router-view></router-view>
        				</keep-alive>
				    </router-link>
		          </h1>
		        </div>
			</div>
			<div class="line"></div>
			<div class="hotspot">
				<h1 class="list-hotspot-title">今日热点</h1>
				<ul class="hotSpot-list">
		            <li v-for="item in hotSpot" class="hotSpot-item">
		            	<div class="hotSpot-item-list">
		            		<div class="hotSpot-item-list-pic">
		            			<img :src="item.img" class="img"/>
		            		</div>
		            		<div class="hotSpot-item-list-txt">
		            			<h2 class="title">{{item.title}}</h2>
		            			<p class="text">{{item.desc}}</p>
		            		</div>
		            	</div>
		            </li>
		        </ul>
			</div>
			<!--<div class="tab">
			    <router-link tag="div" class="tab-item" to="/home-page">
			        <span class="tab-link">首页</span>
			    </router-link>
			    <router-link tag="div" class="tab-item" to="/shop">
			      <span class="tab-link">商店</span>
			    </router-link>
			    <router-link tag="div" class="tab-item" to="/search">
			      <span class="tab-link">搜索</span>
			    </router-link>
			    <router-link tag="div" class="tab-item" to="/login">
			      <span class="tab-link">我的</span>
			    </router-link>
		  	</div>-->
		  	<div class="copying">
		  		<p>
		  			<span>Copyright </span>
		  			<span> HiMovies.com Inc. All rights reserved.</span>
		  		</p>
		  	</div>
	</div>
</template>
<script>
    import Loading from "base/loading/loading"
	import axios from "axios"
    export default {
    	data(){
    		return{
    			hotList:[],
    			aboutToShow:[],
    			hotSpot:[]
    		}
    	},
	    components:{
	    	Loading
	    },
	    created(){
	      	this._getHot();
	      	this._getAboutToShow();
	      	this._getHotSpot();
	    },
	    methods:{
	      	_getHot(){
	      		axios.get("http://120.78.84.152:8080/api/getHot",{}).then((res)=>{
	      			console.log(res.data)
	      			this.hotList = res.data;
	      		})
	      	},
	      	_getAboutToShow(){
	      		axios.get("http://120.78.84.152:8080/api/getAboutToShow",{}).then((res)=>{
	      			console.log(res.data)
	      			this.aboutToShow = res.data;
	      		})
	      	},
	      	_getHotSpot(){
	      		axios.get("http://120.78.84.152:8080/api/getHotSpot",{}).then((res)=>{
	      			console.log(res.data)
	      			this.hotSpot = res.data;
	      		})
	      	}
	    }
    }
</script>

<style scoped lang="stylus" >
.home-page{
	position:relative
	padding-top:8.5rem
}
.home-page-hot{
	padding:.6rem 1rem
}
.home-page-hot-list{
	height: 27.5rem;
	overflow:hidden
	border-bottom:0.1rem solid #d8d8d8
	position:relative
}
.loading-container{
	position: absolute;
	top: 25%;
	left: 50%;
	margin-left: -5.5rem;
}
.wrapper-conntent{
	height: 100%
    overflow: hidden
}
/*热映部分*/
.list-title{
	width: 100%
	height: 4rem;
	color: #000;
	font:600 1.45rem/4rem "微软雅黑"	
	position:relative
}
.list-hot-item{
	position:absolute
	top:0px
	right:.5rem
}
.hot-more-link img{
	margin-top:1rem
	width: 2rem;
	height:2rem;
}
.home-page-hot-list>ul{
	width: 100%;

}
.hot-item{
	float: left;
	width: 25%;
	/*margin-bottom:1rem ;*/
}
.hot-item .hot-move{
	margin: 0 auto;
	width: 5.8rem;
	height: 12rem;
	position: relative;
}
.hot-item .hot-move .score{
	position: absolute;
	top: 0;
	right: .2rem;
	background: #659D0E;
	font-size: 1rem;
	color: #fff;
	width: 1.5rem;
	line-height: 1.6rem;
	text-align: center;
}
.hot-item .hot-move img{
	width: 100%;
	height: 8rem;
}
.hot-item .hot-move .text{
	text-align: center;
	font-size: 1rem;
	height: 2.8rem;
	overflow: hidden;
}

/*即将上映部分*/
.about-to-show .list-aboutToShow-title{
	width: 100%
	height: 4rem;
	color: #000;
	font:600 1.45rem/4rem "微软雅黑"	
	position:relative
}
.list-aboutToShow-item{
	position:absolute
	top:0px
	right:.5rem	
}
.aboutToShow-more-link img{
	margin-top:1rem
	width: 2rem;
	height: 2rem;
}				
.line{
	width: 100%
	height: 1rem
	background:#f6f6f6		
}	





/*热点部分*/
.hotspot{
	padding:.6rem 0 .6rem 1rem	
}	
.list-hotspot-title{
	color: #000;
	width: 100%
	height: 4rem
	font:600 1.45rem/4rem "微软雅黑"	
}		
.hotSpot-item{
	height: 9.5rem;
}	
.hotSpot-item-list-pic{
	width: 11.5rem;
	height: 8.3em;
	display: flex;
	float: left;
}
.hotSpot-item-list-pic img{
	width: 100%;
}	
.hotSpot-item-list-txt{
	float: left;
	width: 12.5rem;
	margin-left: 1rem;
}
.hotSpot-item-list-txt .title{
	height:2.5rem;
	color: #000;
	font: bold 1.35rem "微软雅黑";
}
@media only screen and (min-width: 375px) {
	.hotSpot-item-list-txt{
		float: left;
		width: 17rem;
		margin-left: 1.7rem;
	}
}
/*footer部分*/	
.tab{
	width: 100%;
	height: 3rem;	

}
.tab-item{
	width: 25%
	height: 3rem
	float:left
	text-align:center	
}
.tab-link{
	display:block
	width: 100%;
	height:3rem;
	color:#1c2635
	font:1.4rem/3rem "微软雅黑"	
}			
.copying{
	width: 100%
	height: 4rem
	background:#f6f6f6
	line-height: 4rem
	text-align:center
	font-size:1.2rem
	color:#ccc	
}


								
</style>


