<template>
	<div class="find">
			<Tabs value="name1" >
		        <TabPane label="新闻" name="name1">
		        	<ul class="findNewsList-list">
						<li v-for="item in findNewsList" class="findNewsList-item" >
						    <div class="findNewsList-move" >
						    	<div class="img">
						    		<a :href="'http://m.mtime.cn/#!/news/movie/'+item.id+'/'">				     
						            	<img v-lazy="item.image" >
						    		</a>
						    	</div>
						        <div class="text">
						        	<p class="title1">{{item.title}}</p>
						        	<p class="title2">{{item.title2}}</p>
						        </div>
						    </div>
						</li>
					</ul>
		        </TabPane>
		        <TabPane label="预告片" name="name2">
		        	<ul class="findTrailerList-list">
						<li v-for="item in findTrailerList" class="findTrailerList-item" >
						    <div class="findTrailerList-move" >
						    	<div class="img">
						    		<a :href="item.hightUrl">				     
						            	<img v-lazy="item.coverImg">
						    		</a>
						    	</div>
						        <div class="text">
						        	<p class="title1">{{item.movieName}}</p>
						        	<p class="title2">{{item.summary}}</p>
						        </div>
						    </div>
						</li>
					</ul>
		        </TabPane>
		    </Tabs>
			<div class="loading-container" v-show="!findTrailerList.length">
        		<loading></loading>
      		</div>
		
	</div>
</template>

<script>
	import Vue from "vue"
	import { Tabs } from 'iview';
	Vue.component('Tabs ', Tabs);
    import Loading from "base/loading/loading"
	import axios from "axios"
	export default {
    	data(){
    		return{
    			findNewsList:[],
    			findTrailerList:[]
    		}
    	},
	    components:{
	    	Loading
	    },
	    created(){
	    	this._getfindNewsList();
	      	this._getFindTrailerList();
	    },
	    methods:{
	    	_getfindNewsList(){
	      		axios.get("http://120.78.84.152:8080/api/getfindNewsList",{}).then((res)=>{
	      			console.log(res.data)
	      			this.findNewsList = res.data;
	      		})
	      	},
	      	_getFindTrailerList(){
	      		axios.get("http://120.78.84.152:8080/api/getFindTrailerList",{}).then((res)=>{
	      			console.log(res.data)
	      			this.findTrailerList = res.data;
	      		})
	      	}
	    }
    }
</script>

<style scoped lang="stylus">

.find{
	position: relative;
	top: 8.5rem;
	padding:0 .8rem
}
.tab{
	overflow: auto;
}
.findNewsList-move{
    height: 8.3rem;
}
.findNewsList-move .img{
    display: flex;
    float: left;
}
.findNewsList-move .img a{
	display: block;
} 
.findNewsList-move a img{
	width: 9rem;
	height: 8rem;
	float: left;
}   
		
.findNewsList-move .text{
	float:left;
	margin-left: .5rem;
	width: 15.5rem;
	position: relative;
}    			
.findNewsList-move .text .title1{
	color: #000;
	font:bold 1.3rem "微软雅黑"
}  
.findNewsList-move .text .title2{
	font-size: 1rem;
	margin-top: .5rem;
	color:#666
}  





.findTrailerList-move{
    height: 8.3rem
}
.findTrailerList-move .img{
    display: flex;
    float: left;
}
.findTrailerList-move .img a{
	display: block;
} 
.findTrailerList-move a img{
	width: 12rem;
	height: 7rem;
	float: left;
}     			
.findTrailerList-move .text{
	float:left;
	margin-left: .5rem;
	width: 12rem;
	height: 8.3rem;
	position: relative;
}   			
.findTrailerList-move .text .title1{
	font:bold 1.4rem "微软雅黑"
	color:#1c2635
}  
.findTrailerList-move .text .title2{
	font-size: 1.2rem;
	margin-top: 1rem;
	color:#666
}    
@media only screen and (min-width:375px ) {
	.findTrailerList{
		position: relative;
		top: 8.8rem;
	   	padding:0 1rem
	}
	.findNewsList-move .text{
		float:left;
		margin-left: 1rem;
		width: 19.2rem;
		height: 4rem;
		position: relative;
	}   
	.findTrailerList-move .text{
		float:left;
		margin-left: 1rem;
		width: 16rem;
		height: 8.5rem;
		position: relative;
	} 	
}
.loading-container{
	position: relative
	left:50%
	top:12rem
	transform: translateX(-50%)
}
	        
</style>


