<template>
	<div class="attention">
			<h1 class="attention-title"><span @click="back" ><img src="../../common/images/back.png"/></span>   即将上映({{aboutToShowList.length}}部)</h1>
			<ul class="attention-list">
				<li v-for="item in aboutToShowList" class="attention-item" >
				    <div class="attention-move" >
				    	<div class="img">
				    		<a :href="'http://m.mtime.cn/#!/movie/'+item.id+'/'">				     
				            	<img v-lazy="item.image">
				       
				    		</a>
				    	</div>
				        <div class="text">
				        	<p class="name">{{item.title}}<span>({{item.releaseDate}})</span></p>
				        	<p class="type"><span>{{item.wantedCount}}</span> 人想看 - {{item.type}}</p>
				        	<p class="director">导演   :   {{item.director}}</p>
				        	<p class="actor">演员:{{item.actor1}} , {{item.actor2}}</p>
						    <a :href="'http://m.mtime.cn/#!/movie/'+item.id+'/'" class="trailer">预告片</a>
				        </div>
				    </div>
				</li>
			</ul>
			<div class="loading-container" v-show="!aboutToShowList.length">
        		<loading></loading>
      	</div>
		
		
	</div>
</template>

<script>
    import Loading from "base/loading/loading"
	import axios from "axios"
	export default {
    	data(){
    		return{
    			aboutToShowList:[]
    		}
    	},
	    components:{
	    	Loading
	    },
	    created(){
	      	this._getAboutToShow();
	    },
	    methods:{
	      	_getAboutToShow(){
	      		axios.get("http://120.78.84.152:8080/api/getAboutToShow",{}).then((res)=>{
	      			console.log(res.data)
	      			this.aboutToShowList = res.data;
	      		})
	      	},
	      	back(){
         		this.$router.back()
        	}
	    }
    }
</script>

<style scoped lang="stylus">
.attention{
	position: relative;
	top: 8.5rem;
   	padding:0 .5rem;
   	
}
.attention-title{
	width: 100%
	height: 3rem;
	color: #000;
	font:bolder 1.6rem/3rem "微软雅黑"
	position:relative
}
.attention-title img{
	width: 1.6rem;
	position: relative;
	top: .3rem;
}
.attention-move{
    height: 13rem;
    padding:.8rem 0 .5rem;

}
.attention-move .img{
	display: flex;
	float: left;
}
.attention-move a{
	float: left;
	display: block;
	width: 8rem;
	height: 11.5rem;
	font-size: 1.2rem;
} 
.attention-move a img{
	width: 8rem;
	height: 11.5rem;
}    
.attention-move a span{
	float: left;
} 			
.attention-move .text{
	float:left;
	margin-left: 1rem;
	width: 16.5rem;
	height: 11.5rem;
	position: relative;
}   			
.attention-move .text .name{
	font:bold 1.5rem "微软雅黑"
	color:#1c2635
}    
.attention-move .text .name span{
	font-size: 1.2rem;
	color: #666;
}
.attention-move .text .type{
	margin-top: .2rem;
	font-size: 1.2rem;
}
.attention-move .text .type span{
	color: #ff8600;
}
.attention-move .text .director{
	/*margin-top: .8rem;*/
	font-size: 1.2rem;
}
.attention-move .text .actor{
	/*margin-top: .5rem;*/
	font-size: 1.2rem;
}
.attention-move .text .trailer{
	position: absolute;
	bottom: .5rem;
	right: 1rem;
	width: 8rem;
	height: 2.8rem;
	line-height:2.8rem;
	text-align: center;
	border-radius:1.4rem;
	border: .1rem solid #659c0d;
	color: #659c0d;
}
@media only screen and (min-width: 375px) {
	.attention-move .text{
		float:left;
		margin-left: 1rem;
		width: 21rem;
		height: 11.5rem;
		position: relative;
	}  
	.attention-move .text .trailer{
		position: absolute;
		bottom: .5rem;
		right: 1rem;
		width: 10rem;
		height: 3.6rem;
		line-height: 3.6rem;
		text-align: center;
		border-radius:1.8rem;
		border: .1rem solid #659c0d;
		color: #659c0d;
	}
}
.loading-container{
	position: relative
	left:50%
	top:10rem
	transform: translateX(-50%)
}
	        
</style>


