<template>
	<div class="xingzhan">

		<div class="tabList">
      		<ul class="xingzhan-list">
				<li v-for="item in xingzhanList" class="xingzhan-item" >
					<span class="iconText">{{item.iconText}}</span>
					<img v-lazy="item.imageSrc"/>	
					<p>
						<span class="name">{{item.name}}</span>
						<span class="price">￥{{item.minSalePrice/100}} <s>{{item.marketPrice/100>0?'￥'+item.marketPrice/100:" "}}</s></span>
					</p>
				</li>
			</ul>
			<p class="loading">奋力加载中...</p>
      	</div>
      	
	</div>
</template>

<script>
    import Loading from "base/loading/loading"
	import axios from "axios"
	export default {
    	data(){
    		return{
    			xingzhanList:[]
    		}
    	},
	    components:{
	    	Loading
	    },
	    created(){
	      	this._getXingzhan();
	    },
	    methods:{
	      	_getXingzhan(){
	      		axios.get("http://120.78.84.152:8080/api/getXingzhan",{}).then((res)=>{
	      			console.log(res.data.goodsList)
	      			this.xingzhanList = res.data.goodsList;
	      		})
	      	},
	      	back(){
         		this.$router.back()
        	}
	    }
    }
</script>

<style>

.xingzhan{
	float: left;
	position: relative;
	top:8.5rem;
	width: 100%;
}

.t{
	text-align: center;
	background: #f1f1f1;
	width: 100%;
	height: 3rem;
	position: relative;
	font:bold 1.5rem/3rem "微软雅黑";
}
.t span img{
	position: absolute;
	top: .7rem;
	left: .5rem;
	width: 1.6rem;
}
.loading{
	float: left;
	text-align: center;
	font-size: 1.6rem;
	width: 100%;
	height: 4rem;
	line-height: 4rem;
	background: #f1f1f1;
}
.tabList{
	float: left;
	width: 100%;

}     
.xingzhan-list{
	width: 100%;
	float: left;
	background: #f1f1f1;
}
.xingzhan-item{
	float: left;
	padding: .2rem .2rem;
	width: 50%;
	position: relative;
}
.xingzhan-item .iconText{
	position: absolute;
	top: .2rem;
	left: .1rem;
	width: 2.5rem;
	line-height: 1.5rem;
	text-align: center;
	font-size: 1rem;
	color: #fff;
	background: #25ACBD;
	
}
.xingzhan-item img{
	width: 100%;
	display: block;
}
.xingzhan-item p{
	padding: .1rem .4rem;
	width: 100%;
	height: 6rem;
	background: #fff;
	
}
.xingzhan-item p .name{
	padding: 0 .5rem;
	height: 3.2rem;
	display: block;
	font-size:1.2rem ;
}
.xingzhan-item p .price{
	display: block;
	padding-left: .5rem;
	font:1.4rem "微软雅黑";
	color: #ff8600;
}
.xingzhan-item p .price s{
	color: #ccc;
	font:1.2rem "微软雅黑";
}
</style>


