<template>
	<div class="hot-more">
		
				<ul class="hot-more-list">
					<li v-for="item in hotList" class="hot-more-item" >
					    <div class="hot-move" >
					    	<a :href="'http://m.mtime.cn/#!/movie/'+item.id+'/'">				     
					            	<img v-lazy="item.img">
					    	</a>
					        <div class="text">
					        	<p class="name" v-html="item.t" ></p>
					        	<p class="introduce">{{item.commonSpecial}}</p>
					        	<ul class="ms">
					        		<li><span v-html="item.versions[0]?item.versions[0]['version']:'2D/3D'"></span></li>
					        		<li><span v-html="item.versions[2]?item.versions[2]['version']:'中国巨幕'"></span></li>
					        	</ul>
					        	<p class="status">{{item.cC}}家影院上映{{item.NearestShowtimeCount}}场</p>
					        	<router-link tag="div" class="purchase-item" to="/purchase">
							       	<span class="purchase-link">购买</span>
							        <keep-alive>
			          					<router-view></router-view>
			        				</keep-alive>
							    </router-link>
					        </div>
					    </div>
					</li>
				</ul>
				<div class="loading-container" v-show="!hotList.length">
	        		<loading></loading>
	      		</div>
				
		</div>		
				
			
		
	</div>
</template>

<script>

    import Loading from "base/loading/loading"
	import axios from "axios"
	export default {
    	data(){
    		return{
    			hotList:[]
    		}
    	},
	    components:{
	    	Loading
	    },
	    created(){
	      	this._getHot();
	    },
	    methods:{
	      	_getHot(){
	      		axios.get("http://120.78.84.152:8080/api/getHot",{}).then((res)=>{
	      			console.log(res.data)
	      			this.hotList = res.data;
	      		})
	      	}	      	
	    }
    }
</script>

<style scoped lang="stylus">
.hot-more{
	position: relative;
	top:8.5rem
   	padding:0 .9rem
}
.hot-more-content{
	 height: 100%
      overflow: hidden
}
.hot-more-list{
	height: 100%;
}
.hot-move{
	width: 100%;
    height: 13rem
    padding:.8rem 0 1rem
    border-bottom:.1rem solid #f1f1f1
}
.hot-move a{
	display: flex;
	float: left;
} 
.hot-move a img{
	width: 7.8rem;
	height: 11.7rem;
}    			
.hot-move .text{
	float:left;
	margin-left: 1.5rem;
	width: 15.5rem;
	height: 12rem;
	position: relative;
}   
@media only screen and (min-width:375px ) {
	.hot-move .text{
		float:left;
		margin-left: 1.5rem;
		width: 20rem;
		height: 12rem;
		position: relative;
	}   
}			
.hot-move .text .name{
	margin-bottom:.4rem ;
	font:bolder 1.6rem "微软雅黑"
	color:#1c2635
}    
.hot-move .text .introduce{
	color: #659d0e
	font-size: 1.3rem
} 			
.hot-move .text .ms{
	float: left
	margin-top:.5rem
	width: 100%
}     		
.hot-move .text .ms li{
	float:left;
	margin: .2rem;
	color: #659d0e
	border:1px solid #659d0e;
	font-size:1.2rem
}	
.hot-move .text .status{
	float: left;
	margin-top: .6rem;
	color:#999;
	font-size: 1.2rem;
}
.hot-move .text .purchase-item{
	position: absolute;
	bottom: 0;
	right:0rem;
	width: 9rem;
	height:2.8rem;
	line-height:2.8rem;
	text-align: center;
	border-radius:1.5rem;
	background: #ff8600;
	color: #fff;
	font-size: 1.3rem;
}
.loading-container{
	position: absolute
	left:50%
	top:15rem
	transform: translateX(-50%)
}
	        
</style>


