<template>
	<div class="aboutToShow-more">
			<h1 class="follow-title"><span @click="back" ><img src="../../common/images/back.png" /></span>   最受关注({{followList.length}}部)
			    <router-link tag="div" class="attention-item" to="/attention">
					<span class="attention-link"><i>more</i><img src="../../common/images/go.png"/></span>
					<keep-alive>
          				<router-view></router-view>
        			</keep-alive>
				</router-link>
			</h1>
			<ul class="aboutToShow-more-list">
				<li v-for="item in followList" class="aboutToShow-more-item" >
				    <div class="aboutToShow-move" >
				    	<div class="img">
				    		<a :href="'http://m.mtime.cn/#!/movie/'+item.id+'/'">				     
				            	<img v-lazy="item.image">
				            	<span>{{item.rYear}}</span>
				    		</a>
				    	</div>
				        <div class="text">
				        	<p class="name">{{item.title}}<span>({{item.releaseDate}})</span></p>
				        	<p class="type"><span>{{item.wantedCount}}</span> 人想看 - {{item.type}}</p>
				        	<p class="director">导演   :   {{item.director}}</p>
				        	<p class="actor">演员:{{item.actor1}} , {{item.actor2}}</p>
				        	<router-link tag="div" class="purchase-item" to="/purchase">
						       	<span class="purchase-link">超前预售</span>
						        <keep-alive>
		          					<router-view></router-view>
		        				</keep-alive>
						    </router-link>
						    <a :href="'http://m.mtime.cn/#!/movie/'+item.id+'/'" class="trailer">预告片</a>
				        </div>
				    </div>
				</li>
			</ul>
			<div class="loading-container" v-show="!followList.length">
        		<loading></loading>
      		</div>
		
		
	</div>
</template>

<script>
    import Loading from "base/loading/loading"
	import axios from "axios"
	export default {
    	data(){
    		return{
    			followList:[]
    		}
    	},
	    components:{
	    	Loading
	    },
	    created(){
	      	this._getFollow();
	    },
	    methods:{
	      	_getFollow(){
	      		axios.get("http://120.78.84.152:8080/api/getFollow",{}).then((res)=>{
	      			console.log(res.data)
	      			this.followList = res.data;
	      		})
	      	},
	      	back(){
         		this.$router.back()
        	}
	    }
    }
</script>

<style scoped lang="stylus">
.aboutToShow-more{
	position:relative;
	top:8.5rem
}
.follow-title{
	width: 100%
	height:3rem;
	color: #000;
	font:bolder 1.6rem/3rem "微软雅黑"
	position:relative
}
.follow-title img {
	width: 1rem;
}
.follow-title img:first-child {
	width: 1.6rem;
	position: relative;
	top: .3rem;
}
.attention-item{
	position:absolute
	top:0
	right:.5rem
}
.attention-link{
	font:bold 1.2rem "宋体";	
}
.attention-link i{
	font: 1.2rem "微软雅黑"
}
.attention-link img{
	position: relative;
	top: .1rem;
}
.aboutToShow-more{
	position: relative
   	padding:0 .8rem;
} 
.aboutToShow-more-content{
	height:100%
    overflow:hidden
}
.aboutToShow-more-list{
	width: 100%
}
.aboutToShow-move{
    height: 14rem;
    margin-bottom: 1.5rem;
    padding:8px 0 0
}
.img{
	display: flex;
	float: left;
}
.aboutToShow-move a{
	float: left;
	display: block;
	width: 8rem;
	height: 14rem;
	font-size: 1.2rem;
} 
.aboutToShow-move a img{
	width: 8rem;
	height: 11.5rem;
}     		

.aboutToShow-move .text{
	float:left;
	margin-left: .5rem;
	width: 16.5rem;
	height: 14rem;
	position: relative;
} 
@media only screen and (min-width: 375px) {
	.aboutToShow-move .text{
		float:left;
		margin-left:1.5rem;
		width: 19.6rem;
		height: 14rem;
		position: relative;
	} 
	.aboutToShow-move .text .purchase-item{
		position: absolute;
		bottom: .6rem!important;
		left:0;
		width: 9rem!important;
		height: 3rem!important;
		line-height: 3rem!important;
		text-align: center;
		border-radius:1.8rem;
		background: #ff8600;
		color: #fff;
		font-size: 1.5rem;
	}
	
	.aboutToShow-move .text .trailer{
		position: absolute;
		bottom: .6rem!important;
		right: 1rem;
		width: 9rem!important;
		height: 3rem!important;
		line-height: 3rem!important;
		text-align: center;
		border-radius:1.8rem;
		border: .1rem solid #659c0d;
		color: #659c0d;
	}
}  			
.aboutToShow-move .text .name{
	font:bold 1.6rem "微软雅黑"
	color:#1c2635
}    
.aboutToShow-move .text .name span{
	font-size: 1.2rem;
	color: #666;
}
.aboutToShow-move .text .type{
	margin-top: .5rem;
	font-size: 1.2rem;
}
.aboutToShow-move .text .type span{
	color: #ff8600;
}
.aboutToShow-move .text .director{
	margin-top: .4rem;
	font-size: 1.2rem;
}
.aboutToShow-move .text .actor{
	/*margin-top: .5rem;*/
	font-size: 1.2rem;
}
.aboutToShow-move .text .introduce{
	color: #659d0e
	font-size: 1.6rem
} 			
.aboutToShow-move .text .ms{
	float: left
	margin-top:.5rem
	width: 100%
}     		
.aboutToShow-move .text .ms li{
	float:left;
	margin: .2rem;
	color: #659d0e
	border:.1rem solid #659d0e;
	font-size:1.4rem
}	
.aboutToShow-move .text .status{
	float: left;
	margin-top: 1rem;
	color:#999;
	font-size: 1.4rem;
}
.aboutToShow-move .text .purchase-item{
	position: absolute;
	bottom: 1rem;
	left:0;
	width: 7.5rem;
	height: 3rem;
	line-height: 3rem;
	text-align: center;
	border-radius:1.8rem;
	background: #ff8600;
	color: #fff;
	font-size: 1.5rem;
}

.aboutToShow-move .text .trailer{
	position: absolute;
	bottom: 1rem;
	right: .2rem;
	width: 7.5rem;
	height: 3rem;
	line-height: 3rem;
	text-align: center;
	border-radius:1.8rem;
	border: .1rem solid #659c0d;
	color: #659c0d;
}

.loading-container{
	position: absolute
	left:50%
	top:15rem
	transform: translateX(-50%)
}
	        
</style>


