import Vue from 'vue'
import VueRouter from 'vue-router'
import HomePage from "components/home-page/home-page"

import Shop from "components/shop/shop"
import Mowan from "components/mowan/mowan"
import Shuma from "components/shuma/shuma"
import Jiaju from "components/jiaju/jiaju"
import Fushi from "components/fushi/fushi"
import Xingzhan from "components/xingzhan/xingzhan"
import Manwei from "components/manwei/manwei"
import Fuchao from "components/fuchao/fuchao"
import All from "components/all/all"
import Find from "components/find/find"
import HotMore from "components/hot-more/hot-more"
import AboutToShowMore from "components/aboutToShow-more/aboutToShow-more"
import Attention from "components/attention/attention"
import Login from "components/login/login"
import Zhuce from "components/zhuce/zhuce"
import User from "components/user/user"
//import Search from "components/search/search"

Vue.use(VueRouter);
export default new VueRouter({
  routes:[
    {
      path:'/',
      redirect:"/home-page"
    },{
      path:"/home-page",
      component:HomePage
    },{
      path:"/shop",
      component:Shop
    },{
    	path:"/mowan",
      component:Mowan
    },{
    	path:"/shuma",
      component:Shuma
    },{
    	path:"/jiaju",
      component:Jiaju
    },{
    	path:"/fushi",
      component:Fushi
    },{
    	path:"/xingzhan",
      component:Xingzhan
    },{
    	path:"/manwei",
      component:Manwei
    },{
    	path:"/fuchao",
      component:Fuchao
    },{
    	path:"/all",
      component:All
    },{
      path:"/find",
      component:Find
    },{
      path:"/hot-more",
      component:HotMore
    },{
    	path:"/aboutToShow-more",
    	component:AboutToShowMore
    },{
    	path:"/attention",
    	component:Attention
    },{
    	path:"/login",
    	component:Login
    },{
    	path:"/zhuce",
    	component:Zhuce
    },{
    	path:"/user",
    	component:User
    }
    
    
    
  ]
})



